<template>
  <page
    @on-ready="onReady"
    ref="page"
    class="references-page common-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
  >
    <van-nav-bar slot="header" id="header" title="参考资料" left-arrow @click-left="onClickLeft">
    </van-nav-bar>

    <van-cell-group>
      <van-cell title="维修指引"></van-cell>
      <van-cell @click="handlePreview(item)" class="pdf-item" :title="item.name" :value="item.version" is-link clickable v-for="item in repairPoint" :key="item.id">
        <i class="icon-file" slot="icon"></i>
      </van-cell>
      <van-cell class="blank-item" title="暂无数据" v-if="!repairPoint.length"></van-cell>
    </van-cell-group>

    <van-cell-group>
      <van-cell title=" 设备图纸"></van-cell>
      <van-cell @click="handlePreview(item)" class="pdf-item" :title="item.name" :value="item.version" is-link clickable v-for="item in equipmentDrawing" :key="item.id">
        <i class="icon-file" slot="icon"></i>
      </van-cell>
      <van-cell class="blank-item" title="暂无数据" v-if="!equipmentDrawing.length"></van-cell>
    </van-cell-group>

    <van-cell-group>
      <van-cell title="故障排查&恢复"></van-cell>
      <van-cell @click="handlePreview(item)" class="pdf-item" :title="item.name" :value="item.version" is-link clickable v-for="item in faultCheck" :key="item.id">
        <i class="icon-file" slot="icon"></i>
      </van-cell>
      <van-cell class="blank-item" title="暂无数据" v-if="!faultCheck.length"></van-cell>
    </van-cell-group>

    <van-cell-group>
      <van-cell title="其他"></van-cell>
      <van-cell @click="handlePreview(item)" class="pdf-item" :title="item.name" :value="item.version" is-link clickable v-for="item in other" :key="item.id">
        <i class="icon-file" slot="icon"></i>
      </van-cell>
      <van-cell class="blank-item" title="暂无数据" v-if="!other.length"></van-cell>
    </van-cell-group>
  </page>
</template>

<script>
import Page from '../../components/Page'
import io from '../../api'
import navigate from '../../utils/navigate.js'

export default {
  components: {
    Page
  },
  data: () => ({
    headerHeightVW: 0,
    id: null,
    repairPoint: [],
    equipmentDrawing: [],
    faultCheck: [],
    other: []
  }),
  methods: {
    onReady (header, main) {
      this.headerHeightVW = (header.h + 1) * 100 / header.w

      this.id = navigate.getParam('id')

      this.id && this.loadData()
    },

    loadData () {
      this.$toast.loading({
        duration: 0,
        loadingType: 'spinner'
      })

      io.getOrderReferences({
        id: this.id
      }).then(data => {
        this.repairPoint = data.repair_point
        this.equipmentDrawing = data.equipment_drawing
        this.faultCheck = data.fault_check
        this.other = data.other

        this.$toast.clear()
      }).catch(msg => {
        this.$toast(msg)
      })
    },

    handlePreview (item) {
      navigate.open('browser', {title: item.name, url: item.url})
    },

    handlePlayVideo (item) {
      api.openVideo({
        url: item.url
      })
    },

    handlePlayImages (index, images) {
      localStorage.setItem('photos', JSON.stringify(images))
      navigate.open('photoViewer', {index: index})
    },

    onClickLeft () {
      navigate.back()
    }
  }
}
</script>

<style lang="scss">
.icon-file{
  background: url(../../assets/images/pdf.png) no-repeat;
  background-size: 100% 100%;
  width: 20px;
  height: 20px;
  margin-right: 12px;
}

.references-page {
  .pdf-item{
    .van-cell__title{
      width: 70%;
      flex: auto;
      font-size: 14px;
    }
  }

  .blank-item{
    .van-cell__title{
      color: #969799;
      font-size: 14px;
    }
  }
}
</style>
